@import "./global";

body.toplevel_page_wpcafe {
    @apply font-inter bg-neutral-100;
}

.wpc-reservation-form-root {
    @apply font-inter;
}

.toplevel_page_wpcafe #wpcontent {
    @apply p-0;
}

.toplevel_page_wpcafe *,
.toplevel_page_wpcafe ::before,
.toplevel_page_wpcafe ::after,
.wpc-reservation-form-root *,
.wpc-reservation-form-root ::before,
.wpc-reservation-form-root ::after {
    box-sizing: border-box;
}

.toplevel_page_wpcafe img,
.wpc-reservation-form-root img {
    max-width: 100%;
    display: block;
    object-fit: cover;
}

.toplevel_page_wpcafe ul,
.wpc-reservation-form-root ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

.toplevel_page_wpcafe .wcf-btn,
.wpc-reservation-form-root .wcf-btn {
    @apply font-medium text-base;
}

.toplevel_page_wpcafe.wpcafe-active {
    #adminmenu,
    #adminmenuback,
    #adminmenuwrap {
        display: none !important;
    }

    #wpcontent {
        margin-left: 0 !important;
    }

    #wpfooter {
        margin-left: 0 !important;
    }
}

.toplevel_page_wpcafe #adminmenumain,
body.wpcafe-woo-admin-page #adminmenumain {
    display: none;
}
