@import "./fonts.css";

@import "tailwindcss";
@import "tw-animate-css";

@custom-variant dark (&:is(.dark *));

.toplevel_page_wpcafe,
.wpc-reservation-form-root {
    --foreground: oklch(0.145 0 0);
    --card: oklch(1 0 0);
    --card-foreground: #09090b;
    --popover: oklch(1 0 0);
    --popover-foreground: oklch(0.145 0 0);
    --primary-foreground: #ffffff;
    --secondary: oklch(0.97 0 0);
    --secondary-foreground: oklch(0.205 0 0);
    --muted: oklch(0.97 0 0);
    --muted-foreground: #71717a;
    --accent: oklch(0.97 0 0);
    --accent-foreground: oklch(0.205 0 0);
    --destructive: oklch(0.577 0.245 27.325);
    --destructive-foreground: oklch(0.577 0.245 27.325);
    --border: oklch(0.922 0 0);
    --ring: oklch(0.708 0 0);
    --chart-1: oklch(0.646 0.222 41.116);
    --chart-2: oklch(0.6 0.118 184.704);
    --chart-3: oklch(0.398 0.07 227.392);
    --chart-4: oklch(0.828 0.189 84.429);
    --chart-5: oklch(0.769 0.188 70.08);
    --radius: 0.625rem;
    --sidebar: oklch(0.985 0 0);
    --sidebar-foreground: oklch(0.145 0 0);
    --sidebar-primary: oklch(0.205 0 0);
    --sidebar-primary-foreground: oklch(0.985 0 0);
    --sidebar-accent: oklch(0.97 0 0);
    --sidebar-accent-foreground: oklch(0.205 0 0);
    --sidebar-border: oklch(0.922 0 0);
    --sidebar-ring: oklch(0.708 0 0);

    --background: #ffffff;

    /* Brand Colors (Primary) */
    --primary: #f45925;
    --primary-400: #cf4c1f;
    --primary-300: #f6774c;
    --primary-200: #fab8a1;
    --primary-100: #feeee9;

    /* Base Colors */
    --black: #1a1a1a;
    --white: #ffffff;

    /* Neutral Grays */
    --neutral-600: #1d222b;
    --neutral-500: #414454;
    --neutral-400: #6b7280;
    --neutral-300: #e6e6f0;
    --neutral-200: #eff0f6;
    --neutral-100: #f6f8f9;
    --neutral-light: #525266;
    --neutral-light-dark: #36364d;

    /* Data Visual Colors */
    --visual-400: #a5a9be;
    --visual-300: #d6d5df;
    --visual-200: #f8f8f8;
    --visual-100: #f6f6f6;

    /* Semantic Colors */
    --success: #10b981;
    --info: #0ea5e9;
    --warning: #f59e0b;
    --danger: #ef4444;
}

.toplevel_page_wpcafe.dark,
.wpc-reservation-form-root.dark {
    --background: oklch(0.145 0 0);
    --foreground: oklch(0.985 0 0);
    --card: oklch(0.145 0 0);
    --card-foreground: oklch(0.985 0 0);
    --popover: oklch(0.145 0 0);
    --popover-foreground: oklch(0.985 0 0);
    --primary: oklch(0.985 0 0);
    --primary-foreground: oklch(0.205 0 0);
    --secondary: oklch(0.269 0 0);
    --secondary-foreground: oklch(0.985 0 0);
    --muted: oklch(0.269 0 0);
    --muted-foreground: oklch(0.708 0 0);
    --accent: oklch(0.269 0 0);
    --accent-foreground: oklch(0.985 0 0);
    --destructive: oklch(0.396 0.141 25.723);
    --destructive-foreground: oklch(0.637 0.237 25.331);
    --border: oklch(0.269 0 0);
    --input: oklch(0.269 0 0);
    --ring: oklch(0.439 0 0);
    --chart-1: oklch(0.488 0.243 264.376);
    --chart-2: oklch(0.696 0.17 162.48);
    --chart-3: oklch(0.769 0.188 70.08);
    --chart-4: oklch(0.627 0.265 303.9);
    --chart-5: oklch(0.645 0.246 16.439);
    --sidebar: oklch(0.205 0 0);
    --sidebar-foreground: oklch(0.985 0 0);
    --sidebar-primary: oklch(0.488 0.243 264.376);
    --sidebar-primary-foreground: oklch(0.985 0 0);
    --sidebar-accent: oklch(0.269 0 0);
    --sidebar-accent-foreground: oklch(0.985 0 0);
    --sidebar-border: oklch(0.269 0 0);
    --sidebar-ring: oklch(0.439 0 0);
}

@theme inline {
    --color-background: var(--background);
    --color-foreground: var(--foreground);
    --color-card: var(--card);
    --color-card-foreground: var(--card-foreground);
    --color-popover: var(--popover);
    --color-popover-foreground: var(--popover-foreground);
    --color-primary-foreground: var(--primary-foreground);
    --color-secondary: var(--secondary);
    --color-secondary-foreground: var(--secondary-foreground);
    --color-muted: var(--muted);
    --color-muted-foreground: var(--muted-foreground);
    --color-accent: var(--accent);
    --color-accent-foreground: var(--accent-foreground);
    --color-destructive: var(--destructive);
    --color-destructive-foreground: var(--destructive-foreground);
    --color-border: var(--border);
    --color-input: var(--visual-400);
    --color-ring: var(--ring);
    --color-chart-1: var(--chart-1);
    --color-chart-2: var(--chart-2);
    --color-chart-3: var(--chart-3);
    --color-chart-4: var(--chart-4);
    --color-chart-5: var(--chart-5);
    --radius-sm: calc(var(--radius) - 4px);
    --radius-md: calc(var(--radius) - 2px);
    --radius-lg: var(--radius);
    --radius-xl: calc(var(--radius) + 4px);
    --color-sidebar: var(--sidebar);
    --color-sidebar-foreground: var(--sidebar-foreground);
    --color-sidebar-primary: var(--sidebar-primary);
    --color-sidebar-primary-foreground: var(--sidebar-primary-foreground);
    --color-sidebar-accent: var(--sidebar-accent);
    --color-sidebar-accent-foreground: var(--sidebar-accent-foreground);
    --color-sidebar-border: var(--sidebar-border);
    --color-sidebar-ring: var(--sidebar-ring);

    /* Brand Colors (Primary) */
    --color-primary: var(--primary);
    --color-primary-400: var(--primary-400);
    --color-primary-300: var(--primary-300);
    --color-primary-200: var(--primary-200);
    --color-primary-100: var(--primary-100);

    /* Base Colors */
    --color-black: var(--black);
    --color-white: var(--white);

    /* Neutral Grays */
    --color-neutral-600: var(--neutral-600);
    --color-neutral-500: var(--neutral-500);
    --color-neutral-400: var(--neutral-400);
    --color-neutral-300: var(--neutral-300);
    --color-neutral-200: var(--neutral-200);
    --color-neutral-100: var(--neutral-100);
    --color-neutral-light: var(--neutral-light);
    --color-neutral-light-dark: var(--neutral-light-dark);

    /* Data Visual Colors */
    --color-visual-400: var(--visual-400);
    --color-visual-300: var(--visual-300);
    --color-visual-200: var(--visual-200);
    --color-visual-100: var(--visual-100);

    /* Semantic Colors */
    --color-success: var(--success);
    --color-info: var(--info);
    --color-warning: var(--warning);
    --color-danger: var(--danger);

    --color-concitional-stock: #e4e4e7;
    --color-light-gray: #fcfcfc;

    /* Tooltip Colors */
    --color-tooltip-bg: #000000;
    --color-tooltip-text: #ffffff;
    --color-tooltip-border: #374151;

    --font-inter: "Inter", "sans-serif";
}

@layer base {
    .toplevel_page_wpcafe,
    .toplevel_page_wpcafe *,
    .toplevel_page_wpcafe ::before,
    .toplevel_page_wpcafe ::after,
    .wpc-reservation-form-root,
    .wpc-reservation-form-root *,
    .wpc-reservation-form-root ::before,
    .wpc-reservation-form-root ::after {
        box-sizing: border-box;
    }
    body {
        @apply !pointer-events-auto;
    }
    body.toplevel_page_wpcafe,
    .wpc-reservation-form-root {
        @apply bg-background text-foreground;
    }
    body.toplevel_page_wpcafe img,
    .wpc-reservation-form-root img {
        @apply max-w-full h-auto block object-cover;
    }
    body.toplevel_page_wpcafe ul,
    .wpc-reservation-form-root ul {
        @apply list-none m-0 p-0;
    }
}

@layer components {
    /* Input placeholder styling  */
    .toplevel_page_wpcafe input::placeholder,
    .toplevel_page_wpcafe textarea::placeholder,
    .wpc-reservation-form-root input::placeholder,
    .wpc-reservation-form-root textarea::placeholder {
        color: #b4b1b1 !important;
    }

    /* Scrollbar styling */
    /* Custom scrollbar for all scrollable elements in WP Cafe admin */

    .toplevel_page_wpcafe ::-webkit-scrollbar,
    .wpc-reservation-form-root ::-webkit-scrollbar {
        width: 6px;
        height: 6px;
        background-color: transparent;
    }

    .toplevel_page_wpcafe ::-webkit-scrollbar-track,
    .wpc-reservation-form-root ::-webkit-scrollbar-track {
        background-color: #f5f5f5;
        /* border-radius: 3px; */
    }

    .toplevel_page_wpcafe ::-webkit-scrollbar-thumb,
    .wpc-reservation-form-root ::-webkit-scrollbar-thumb {
        background-color: var(--visual-400);
        border-radius: 3px;
        border: 1px solid #f5f5f5;
        transition: background-color 0.2s ease;
    }

    .toplevel_page_wpcafe ::-webkit-scrollbar-thumb:hover,
    .wpc-reservation-form-root ::-webkit-scrollbar-thumb:hover {
        background-color: var(--visual-400);
    }
    .toplevel_page_wpcafe,
    .wpc-reservation-form-root {
        .wcf-btn {
            font-weight: 500;
            font-size: 1rem;
        }

        .wcf-custom-shadow {
            box-shadow:
                0 1px 2px 0 rgba(0, 0, 0, 0.1),
                0 1px 1px 0 rgba(0, 0, 0, 0.06);
        }
    }
}
